const TestInvite = () => {

  const [code, setCode] = useState(null);

  const { response, setParams, fetch } = useAxios('/user/invite', true, null );

  useEffect( () => {
    let res = response;
    if(res != null){
      setCode(res.data[0]['INVITE_CODE']);
    }
  }, [response])

  return(
    <>
      <button onClick={() => fetch()}></button>
      <div>
      {
        code != null &&
        <span>{code}</span>
      }
      </div>
    </>
  )
}

